import React from 'react'
import Transition from '../components/Transition'

export default function Home() {
    return (
        <>
            <div className='wrapper'>
                <div className={'home'}>
                    <h1>Joshua Keslar</h1>
                    <h4>Software Developer</h4>
                </div>
                <Transition image={'../static/darkToLight.png'} />
            </div>

            <style jsx>{`
        .home {
            background-color: #141301;
            height: 900px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center
        }
        .wrapper {
            width: 100%;
        }
        h1 {
            color: #e5e7e6;
            font-size: 6rem;
        }
        h4 {
            color: #b7b5b3;
            font-size: 2.5rem;
            margin-top: 35px;
        }
      `}</style>
        </>
    )
}
