import React from 'react'
import Navigation from '../components/Navigation'
import Section from '../components/Section'
import Home from '../components/Home'
import GlobalStyles from '../components/GlobalStyles'

export default function index() {
    return (
        <div>
            <GlobalStyles />
            <Navigation />
            <Home />
            <Section 
                title='Skills'
                id='skills'
                image={'../static/lightToDark.png'}
                classNameProp='light'
                dark={false}
                skills
            />
            <Section 
                title='Portfolio'
                id='portfolio'
                image={'../static/darkToLight.png'}
                classNameProp='dark'
                dark
                portfolio
        
            />
            <Section 
                title='Resume'
                id='resume'
                image={'../static/lightToDark.png'}
                classNameProp='light'
                dark={false}
                resume
            />
            <Section 
                title='Contact'
                id='contact'
                image={'../static/darkToLight.png'}
                classNameProp='dark'
                dark
                contact
            />
            
        </div>
    )
}
